import { generalRoute, redirectMyPage } from "./../Router";
export const currentLocation = (path: string) => {
	const login = {
		path: "/login",
		name: "",
	};
	const main = {
		path: "/",
		name: "",
	};
	const allRoute = [...generalRoute, ...redirectMyPage, login, main];

	const result = allRoute.filter(item => item.path === path)[0] || [
		{
			path: "",
			name: "",
		},
	];
	return result.name || "";
};
