import React from "react";

/**   true ,   false . */
export const ToggleEvent = ({
	condition,
	children,
}: {
	condition: boolean;
	children: JSX.Element[];
}) => {
	return condition ? children[0] : children[1];
};

export const OnlyTruthyShow = ({
	condition,
	children,
}: {
	condition: boolean;
	children: JSX.Element;
}) => <React.Fragment>{condition && children}</React.Fragment>;
