import { Column, Row } from "components/atoms/Grid";
import { useAuth, useInjection } from "hooks";
import React, { useCallback } from "react";
import { Space, SpaceBorder, SpaceContainer } from "components/atoms/Spacing";
import { Text } from "components/atoms/Message";
/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { mapper } from "models/RootStore";
import { observer } from "mobx-react-lite";
import styled from "@emotion/styled/macro";
import { Icon } from "components/atoms";

const eventFilterList = [
	{ title: "", filterValue: "" },
	{ title: "", filterValue: "" },
	{ title: "", filterValue: "" },
	{ title: "", filterValue: "" },
	{ title: "", filterValue: "" },
];

export const EventFilterBottomSheet = observer(() => {
	const { eventStore } = useInjection(mapper);

	// NOTE : 
	const closePopup = () => {
		eventStore.setEventFilterShow(false);
	};
	// NOTE :   
	const clickFilterMenu = (filterValue: string) => {
		eventStore.setFilter(filterValue);
		eventStore.setEventFilterShow(false);
	};
	return (
		<FilterBackground>
			<FilterContainer>
				<SpaceContainer row={16} column={14}>
					<Column>
						<Row between>
							<Text size={19}></Text>
							<Icon icon="close_black" size={24} event={closePopup} />
						</Row>
					</Column>
					<Space column={14} />
					{eventFilterList.map((item: any, index: number) => (
						<SpaceContainer key={index} column={17}>
							{eventStore.filter == item.title ? (
								<Row onClick={() => clickFilterMenu(item.filterValue)}>
									<Icon icon={"radioButtonOn"} size={20} />
									<Space row={28} />
									<Text size={15} pink>
										{item.title}
									</Text>
								</Row>
							) : (
								<Row onClick={() => clickFilterMenu(item.filterValue)}>
									<Icon icon={"radioButtonOff"} size={20} />
									<Space row={28} />
									<Text size={15}>{item.title}</Text>
								</Row>
							)}
						</SpaceContainer>
					))}
				</SpaceContainer>
			</FilterContainer>
		</FilterBackground>
	);
});

const FilterBackground = styled.div`
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	width: 100vw;
	height: 100vh;
	background-color: rgba(61, 61, 61, 0.8);
	display: flex;
	align-items: flex-end;
`;

const FilterContainer = styled.div`
	z-index: 9998;
	width: 100%;
	background-color: white;
	border-top-left-radius: 16px;
	border-top-right-radius: 16px;
	animation: SlideFromBottom 0.3s linear;
	-webkit-animation: SlideFromBottom 0.3s linear;
	-o-animation: SlideFromBottom 0.3s linear;

	@keyframes SlideFromBottom {
		0% {
			transform: translateY(336px);
		}
		100% {
			transform: translateY(0);
		}
	}
`;
