import { EventType } from "models/entities";

export const EventViews = (self: EventType) => ({
	//      .
	getData: () => {
		return self.response;
	},
	getError: () => {
		return self.error;
	},
	getEventCount: () => {
		return self.eventCount;
	},
	getState: () => {
		return self.state;
	},
	getShowHospitalEvent: () => {
		return self.showHospitalEvent;
	},
});
